.skills_list{display: flex; flex-direction: row;}
.skill_item{width: 30px; height: 30px; background-color: aliceblue; }
.skill_item:nth-child(1)  { background-image: var(--skill-image-ai); }
.skill_item:nth-child(2)  { background-image: var(--skill-image-andst); }
.skill_item:nth-child(3)  { background-image: var(--skill-image-css); }
.skill_item:nth-child(4)  { background-image: var(--skill-image-js); }
.skill_item:nth-child(5)  { background-image: var(--skill-image-react); }
.skill_item:nth-child(6)  { background-image: var(--skill-image-sass); }
.skill_item:nth-child(7)  { background-image: var(--skill-image-figma); }
.skill_item:nth-child(8)  { background-image: var(--skill-image-html5); }
.skill_item:nth-child(9)  { background-image: var(--skill-image-git); }
.skill_item:nth-child(10) { background-image: var(--skill-image-w3c); }
.skill_item:nth-child(11) { background-image: var(--skill-image-ws); }
.skill_item:nth-child(12) { background-image: var(--skill-image-xd); }


